
.page {
  width: 100%;
}

.before {
  content: "";
  position: fixed;
  width: 100%;
  background-color: black;
  z-index: 10000;
  animation: fades 1s forwards;
  opacity: 1;

}

.page-enter-active {

  position: absolute;
  animation: fade-in 1s forwards;
}

.page-exit-active {
  position: absolute;
  animation: fade-out 1s forwards;
}

@keyframes fades {
  0% {
    height: 0vh;
  }
  30% {
    height: 100vh;
  }
  60% {
    height: 100vh;
  }
  100% {
    height: 0vh;
  }
}


@keyframes fade-in {
  0% {
    top: -1000px;
    opacity: 0;
  }
  1% {
    top: 0;
  }
  50% {
    opacity: 0;

  }
  100% {
    opacity: 1;
  }

}

@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


